import Vue from "vue";
import VueRouter from "vue-router";
import Inici from "../views/Inici.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Inici",
    component: Inici,
  },
  {
    path: "/sandra",
    name: "Sandra",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "Sandra" */ "../views/Sandra.vue"),
  },
  {
    path: "/links",
    name: "links",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "Enllaços" */ "../views/Links.vue"),
  },
  {
    path: "/laura",
    name: "Laura",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "Laura" */ "../views/Laura.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/sanlauportfolio/",
  routes,
});

export default router;
